<template>
  <div class="home">
    <v-table
      ref="table"
      height="400"
      key-field="gNo"
      :data="data"
      show-overflow-tooltip
      highlight-current-row
      @selection-change="selectionChange"
      @row-click="rowClick"
    >
      <v-table-column v-if="checked" key="checkbox" fixed="left" type="selection" :width="40" />
      <v-table-column v-else key="radio" :width="40" fixed="left">
        <template slot-scope="scope">
          <el-radio v-model="radio" class="radio" :label="scope.$index">&nbsp;</el-radio>
        </template>
      </v-table-column>
      <v-table-column type="index" :min-width="60" fixed="left" label="">
        <template slot="header"></template>
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </v-table-column>
      <v-table-column label="1" prop="asn" :min-width="100" />
      <v-table-column label="2" :min-width="150">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </v-table-column>
      <v-table-column label="3" prop="city" :min-width="100" />
      <v-table-column label="4" show-overflow-tooltip :min-width="150">
        <template></template>
      </v-table-column>
      <v-table-column label="5" prop="datetime" :min-width="150" />
      <v-table-column label="6" prop="name" :min-width="150" />
      <v-table-column label="7" :min-width="120">
        <template slot-scope="scope">{{ scope.row.message }}</template>
      </v-table-column>
      <v-table-column v-for="(d, i) in forData" :key="i" :label="d.label" :min-width="150" :render-header="renderHeader">
        <template slot-scope="scope">{{ scope.row[d.prop] }}</template>
      </v-table-column>
      <v-table-column label="" :min-width="100">
        <template>
          <el-button type="text" icon="el-icon-edit" />
          <el-button type="text" icon="el-icon-delete" />
        </template>
      </v-table-column>
    </v-table>
    <p>
      <el-button type="primary" @click="checked = !checked">{{ `${checked ? '' : ''}` }}</el-button>
      <el-button type="primary" @click="toggleRowSelection"></el-button>
      <el-button type="primary" @click="toggleAllSelection"></el-button>
    </p>
  </div>
</template>

<script>
import mock from 'mockjs'

export default {
  name: 'Home',
  data() {
    return {
      radio: null,
      checked: true,
      data: [],
      forData: [
        { prop: 'city', label: '' },
        { prop: 'name', label: '' },
        { prop: 'message', label: '' }
      ]
    }
  },
  mounted() {
    setTimeout(() => {
      this.data = mock.mock({
        'array|1000': [
          {
            'message': '@email',
            'name': '@cname',
            'email': '@email',
            'city': '@city',
            'datetime': '@datetime',
            'asn': /asn10000[1-9]/,
            'gNo|+1': 1,
            'contrItem': '5',
            'codeTs': '8516605000'
          }
        ]
      }).array
      console.log(this.data)
    }, 1000)
  },
  methods: {
    renderHeader(h, { column, $index }) {
      return h('div', {}, [column.label, $index - 1])
    },
    selectionChange(val) {
      console.log(val)
      // console.log(`selection change > ${JSON.stringify(val, null, 2)}`)
    },
    rowClick(row, index, event) {
      console.log(`click row > ${JSON.stringify(row, null, 2)}`)
    },
    toggleRowSelection() {
      this.$refs.table.toggleRowSelection(this.data[1])
    },
    toggleAllSelection() {
      this.$refs.table.toggleAllSelection()
    }
  }
}
</script>

<style>
.home{
  padding: 20px 40px;
}
.v-table .cell .el-radio__label{
  display: none;
}
</style>
